import React, { Component } from 'react'
import { Form, Col, Row, Input, DatePicker } from 'antd'
import NumericInput from '../../../../../common/NumericInput'
import DeptSelect from './dept'
import { FormComponentProps } from 'antd/lib/form'
import { formItemLayout, colLayout } from '../../util'

type IProps = FormComponentProps & {}
type IState = {}

class EditForm extends Component<IProps, IState> {
  constructor(props) {
    super(props)
    this.state = {
      deptData: []
    }
  }

  render() {
    const { getFieldDecorator } = this.props.form
    getFieldDecorator('id')
    getFieldDecorator('attachments')
    return (
      <Form {...formItemLayout}>
        <Row gutter={10}>
          <Col {...colLayout}>
            <Form.Item key={'projectName'} label="">
              {getFieldDecorator('projectName', {
                rules: [
                  { required: true, message: '' },
                  {
                    max: 100,
                    message: '100'
                  }
                ]
              })(<Input />)}
            </Form.Item>
          </Col>
          <Col {...colLayout}>
            <Form.Item key={'dept'} label="">
              {getFieldDecorator('dept')(<DeptSelect />)}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={10}>
          <Col {...colLayout}>
            <Form.Item key={'company'} label="">
              {getFieldDecorator('company', {
                rules: [
                  {
                    max: 100,
                    message: '100'
                  }
                ]
              })(<Input />)}
            </Form.Item>
          </Col>
          <Col {...colLayout}>
            <Form.Item key={'projectType'} label="">
              {getFieldDecorator('projectType', {
                rules: [
                  {
                    max: 50,
                    message: '50'
                  }
                ]
              })(<Input />)}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={10}>
          <Col {...colLayout}>
            <Form.Item key={'contractNum'} label="">
              {getFieldDecorator('contractNum', {
                rules: [
                  {
                    max: 50,
                    message: '50'
                  }
                ]
              })(<Input />)}
            </Form.Item>
          </Col>
          <Col {...colLayout}>
            <Form.Item key={'contractTime'} label="">
              {getFieldDecorator('contractTime')(
                <DatePicker style={{ width: '100%' }} format={'YYYY-MM-DD'} />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={10}>
          <Col {...colLayout}>
            <Form.Item key={'money'} label="">
              {getFieldDecorator('money', {
                rules: [{ required: true, message: '' }]
              })(
                <NumericInput
                  onChange={value => {}}
                  tips={''}
                />
              )}
            </Form.Item>
          </Col>
          <Col {...colLayout}>
            <Form.Item key={'desc'} label={''}>
              {getFieldDecorator('desc', {
                rules: [{ max: 500, message: '500' }]
              })(<Input.TextArea />)}
            </Form.Item>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(EditForm)
