import React from 'react'
import {
  Layout,
  Button,
  Form,
  Spin,
  Modal,
  Row,
  Col,
  Table,
  message
} from 'antd'
import { UploadFile } from 'antd/lib/upload/interface'
import { withRouter, RouteComponentProps } from 'react-router-dom'
import IDocmVO from '../type'
import {
  colLayout as customColLayout,
  singleRowFormItemLayout,
  formItemLayout,
  commonTableColumnProps
} from '../util'
import { FormComponentProps } from 'antd/lib/form'
import PayItemForm from './payItemForm'
import Http, { serverPath } from '../../../../common/http'
import { IPayItemVO } from '../type'
import { modulePath } from '../index'
import './index.css'
import FileDown from './filedown'
import PDFPreviewer from './preview/index'

const { Content } = Layout
const colLayout = {
  ...customColLayout,
  style: { height: '48px' }
}

type IProps = RouteComponentProps & {}

interface IState {
  loading: boolean
  data: IDocmVO
  fileList: Array<UploadFile>
  attachmentId: string | undefined
  showPayItemForm: boolean
  payItem: IPayItemVO | undefined
  showPreview: boolean
  fileData: any
}

class View extends React.Component<IProps, IState> {
  constructor(props: IProps) {
    super(props)
    this.state = {
      loading: true,
      data: {} as IDocmVO,
      fileList: [],
      attachmentId: undefined,
      showPayItemForm: false,
      payItem: undefined,
      showPreview: false,
      fileData: undefined
    }
  }

  componentDidMount() {
    this.refreshPage()
  }

  refreshPage() {
    const { match } = this.props
    const id = match.params['id']
    this.setState({ loading: true, showPayItemForm: false }, () => {
      Http.get(`/docm?id=${id}`)
        .then(res => {
          const data = res.data.data
          const fileList = data.attachments.map(attachment => ({
            uid: attachment.id,
            size: 123,
            name: attachment.docName,
            type: 'image',
            status: 'done',
            response: {
              status: '00000000',
              data: [
                {
                  docName: attachment.docName,
                  docPath: attachment.docPath
                }
              ]
            }
          }))
          this.setState({ loading: false, data, fileList })
        })
        .catch(error => {})
    })
  }

  goBack = e => {
    e.preventDefault()
    this.props.history.push(`${modulePath}/list`)
  }

  renderToolBar() {
    return (
      <div style={{ margin: '4px', flex: 1 }}>
        <span style={{ float: 'right' }}>
          <Button
            className={'ele-operation'}
            type={'primary'}
            onClick={event => {
              event.preventDefault()
              const { match } = this.props
              const id = match.params['id']
              this.props.history.push(`${modulePath}/edit/${id}`)
            }}
          >
            
          </Button>
          <Button type={'default'} onClick={this.goBack}>
            
          </Button>
        </span>
      </div>
    )
  }

  handleDeletePayItem = (ids: Array<string> | Array<number>) => {
    Modal.confirm({
      title: '?',
      okText: '',
      cancelText: '',
      onOk: async () => {
        let queryString = ''
        ids.forEach(id => (queryString = queryString.concat(`ids=${id}&`)))
        Http.delete(
          `/docm/pay-item?${queryString.substring(0, queryString.length - 1)}`
        )
          .then(res => {
            message.success('')
            this.refreshPage()
          })
          .catch(err => {})
      }
    })
  }

  renderContent() {
    const { loading, data, fileList } = this.state
    return !loading && data ? (
      <Form {...formItemLayout}>
        <Row gutter={10}>
          <Col {...colLayout}>
            <Form.Item key={'projectName'} label="">
              {data.projectName}
            </Form.Item>
          </Col>
          <Col {...colLayout}>
            <Form.Item key={'dept'} label="">
              {data.dept ? data.dept.name : ''}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={10}>
          <Col {...colLayout}>
            <Form.Item key={'company'} label="">
              {data.company}
            </Form.Item>
          </Col>
          <Col {...colLayout}>
            <Form.Item key={'projectType'} label="">
              {data.projectType}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={10}>
          <Col {...colLayout}>
            <Form.Item key={'contractNum'} label="">
              {data.contractNum}
            </Form.Item>
          </Col>
          <Col {...colLayout}>
            <Form.Item key={'contractTime'} label="">
              {data.contractTime}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={10}>
          <Col {...colLayout}>
            <Form.Item key={'money'} label="">
              {data.money}
            </Form.Item>
          </Col>
          <Col {...colLayout}>
            <Form.Item key={'description'} label={''}>
              {data.desc}
            </Form.Item>
          </Col>
        </Row>
        <Form.Item {...singleRowFormItemLayout} key={'upload'} label={''}>
          {fileList.map((file, index) => (
            <div key={index}>
              <Button
                key={`button_${file.uid}`}
                id={file.uid}
                icon={'paper-clip'}
                type={'link'}
                href={`${serverPath}/doc/pre-view?id=${
                  file.uid
                }&xAuthToken=${sessionStorage.getItem('xAuthToken')}`}
              >
                {file.name}
              </Button>
              <FileDown
                key={`fileDown_${file.uid}`}
                url={`${serverPath}/doc/pre-view?id=${
                  file.uid
                }&xAuthToken=${sessionStorage.getItem('xAuthToken')}`}
                icon={'eye'}
                type={'default'}
                text={''}
                onClick={e => this.setState({ showPreview: true })}
                onLoad={fileData => this.setState({ fileData })}
              />
            </div>
          ))}
        </Form.Item>
      </Form>
    ) : (
      <Spin size={'large'} />
    )
  }

  renderPayItem() {
    const {
      loading,
      data,
      data: { payItems }
    } = this.state
    const columns = [
      {
        ...commonTableColumnProps,
        title: '',
        dataIndex: 'order',
        key: 'order',
        onHeaderCell: column => ({
          style: { textAlign: 'center', width: '3%' }
        })
      },
      {
        ...commonTableColumnProps,
        title: '',
        dataIndex: 'money',
        key: 'money'
      },
      {
        ...commonTableColumnProps,
        title: '',
        dataIndex: 'credentialNum',
        key: 'credentialNum'
      },
      {
        ...commonTableColumnProps,
        title: '',
        dataIndex: 'credentialTime',
        key: 'credentialTime'
      },
      {
        ...commonTableColumnProps,
        title: '',
        dataIndex: 'payTime',
        key: 'payTime'
      },
      {
        ...commonTableColumnProps,
        title: '',
        dataIndex: 'desc',
        key: 'desc'
      },
      {
        ...commonTableColumnProps,
        title: '',
        dataIndex: 'operation',
        key: 'operation',
        render: (text, record) => {
          return (
            <div>
              <Button
                type={'link'}
                onClick={event => {
                  this.setState({ showPayItemForm: true, payItem: record })
                }}
              >
                {''}
              </Button>
              <Button
                type={'link'}
                onClick={this.handleDeletePayItem.bind(this, [record.id])}
              >{``}</Button>
            </div>
          )
        }
      }
    ]
    let sum = 0
    payItems && payItems.forEach(item => (sum = sum + item.money))
    return !loading ? (
      <Table
        columns={columns}
        dataSource={payItems}
        rowKey={'id'}
        size={'small'}
        bordered
        pagination={false}
        title={() => (
          <div style={{ display: 'flex', alignItems: 'center' }}>
            <div style={{ flex: 1 }}>
              <h2>{``}</h2>
            </div>
            <div style={{ flex: 1 }}>
              <h4>{`${sum}`}</h4>
            </div>
            <div style={{ flex: 1 }}>
              <h4>{`${(
                Math.round(((sum * 100) / data.money) * 100) / 100
              ).toFixed(2)}%`}</h4>
            </div>
            <div style={{ flex: 1 }}>
              <Button
                style={{ float: 'right' }}
                type={'ghost'}
                onClick={() => {
                  this.setState({ showPayItemForm: true, payItem: undefined })
                }}
              >
                
              </Button>
            </div>
          </div>
        )}
      />
    ) : (
      <Spin size={'large'} />
    )
  }

  form: React.ReactElement<FormComponentProps> | undefined = undefined
  handleSubmit = () => {
    this.form &&
      this.form.props.form.validateFields((errors, values) => {
        if (!errors) {
          let method
          if (this.state.payItem) {
            //  payItem 
            method = Http.post
          } else {
            method = Http.put
          }
          Object.assign(values, { contractId: this.state.data.id })
          // 
          values['credentialTime'] = values['credentialTime']
            ? values['credentialTime'].format('YYYY-MM-DD')
            : undefined
          values['payTime'] = values['payTime']
            ? values['payTime'].format('YYYY-MM-DD')
            : undefined
          method(`/docm/pay-item`, values)
            .then(res => {
              if (res.data.status === '00000000') {
                this.refreshPage()
                message.success('')
              } else {
                message.error(res.data.message)
              }
            })
            .catch(err => {
              message.info(err.response.data.msg)
            })
        }
      })
  }

  handlePayItemModalCancel = () => {
    this.setState({ showPayItemForm: false })
  }

  renderPayItemModal() {
    const { showPayItemForm, payItem } = this.state
    return (
      <Modal
        title=""
        visible={showPayItemForm}
        onCancel={this.handlePayItemModalCancel}
        onOk={this.handleSubmit}
        destroyOnClose
      >
        <PayItemForm
          data={payItem}
          wrappedComponentRef={form => (this.form = form)}
        />
      </Modal>
    )
  }

  handlePreviewModalCancel = () => {
    this.setState({ showPreview: false })
  }

  renderPreviewModal() {
    const { showPreview } = this.state
    const { fileData } = this.state
    return (
      <Modal
        title=""
        width={'780px'}
        visible={showPreview}
        onCancel={this.handlePreviewModalCancel}
        footer={null}
        destroyOnClose
      >
        <PDFPreviewer fileData={fileData} />
      </Modal>
    )
  }

  render() {
    return (
      <Content>
        <div
          style={{
            margin: '4px 4px 10px',
            display: 'flex',
            alignItems: 'center'
          }}
        >
          {this.renderToolBar()}
        </div>
        <div style={{ margin: '8px' }}>
          {this.renderContent()}
          {this.renderPayItem()}
        </div>
        <div>{this.renderPayItemModal()}</div>
        <div>{this.renderPreviewModal()}</div>
      </Content>
    )
  }
}

export default withRouter(View)
