import React, { Component } from 'react'
import { Form, Input, InputNumber, DatePicker } from 'antd'
import NumericInput from '../../../../../common/NumericInput'
import { FormComponentProps } from 'antd/lib/form'
import { formItemLayout } from '../../util'

const { MonthPicker } = DatePicker

type IProps = FormComponentProps & {}
type IState = {}

class PayItemForm extends Component<IProps, IState> {
  render() {
    const { getFieldDecorator } = this.props.form
    getFieldDecorator('id')
    return (
      <Form {...formItemLayout}>
        <Form.Item key={'order'} label={''}>
          {getFieldDecorator('order', {
            rules: [{ required: true, message: '' }]
          })(<InputNumber />)}
        </Form.Item>
        <Form.Item key={'money'} label={''}>
          {getFieldDecorator('money', {
            rules: [{ required: true, message: '' }]
          })(<NumericInput />)}
        </Form.Item>
        <Form.Item key={'credentialNum'} label={''}>
          {getFieldDecorator('credentialNum', {
            rules: [{ max: 50, message: '50' }]
          })(<Input />)}
        </Form.Item>
        <Form.Item key={'credentialTime'} label={''}>
          {getFieldDecorator('credentialTime')(
            <MonthPicker style={{ width: '100%' }} format={'YYYY-MM'} />
          )}
        </Form.Item>
        <Form.Item key={'payTime'} label={''}>
          {getFieldDecorator('payTime')(
            <DatePicker style={{ width: '100%' }} format={'YYYY-MM-DD'} />
          )}
        </Form.Item>
        <Form.Item key={'desc'} label={''}>
          {getFieldDecorator('desc', {
            rules: [{ max: 500, message: '500' }]
          })(<Input.TextArea />)}
        </Form.Item>
      </Form>
    )
  }
}

export default Form.create()(PayItemForm)
