import React from 'react'
import { Layout, Form, Breadcrumb } from 'antd'
import { withRouter, RouteComponentProps } from 'react-router-dom'
import { UserContext } from '../../../index'
import './index.css'

const { Content } = Layout

const formItemLayout = {
  labelCol: {
    xs: { span: 20 },
    sm: { span: 4 }
  },
  wrapperCol: {
    xs: { span: 20 },
    sm: { span: 16 }
  }
}

// const tailFormItemLayout = {
//   wrapperCol: {
//     xs: {
//       span: 2,
//       offset: 0
//     },
//     sm: {
//       span: 2,
//       offset: 4
//     }
//   }
// }

type IProps = RouteComponentProps & {}

interface IState {}

class Info extends React.Component<IProps, IState> {
  renderContent() {
    return (
      <UserContext.Consumer>
        {userInfo => {
          return (
            <Form {...formItemLayout}>
              <Form.Item key={'username'} label="">
                {userInfo.username}
              </Form.Item>
              <Form.Item key={'phone'} label="">
                {userInfo.phone}
              </Form.Item>
              <Form.Item key={'email'} label="">
                {userInfo.email}
              </Form.Item>
              <Form.Item key={'dept'} label="">
                {userInfo.dept ? userInfo.dept.name : ''}
              </Form.Item>
            </Form>
          )
        }}
      </UserContext.Consumer>
    )
  }

  render() {
    return (
      <div className="layout-content">
        <div className="layout-content-inner">
          <Breadcrumb separator={'>'}>
            <Breadcrumb.Item></Breadcrumb.Item>
            <Breadcrumb.Item></Breadcrumb.Item>
            <Breadcrumb.Item></Breadcrumb.Item>
          </Breadcrumb>
          <Content>
            <div className="view-page-content">{this.renderContent()}</div>
          </Content>
        </div>
      </div>
    )
  }
}

export default withRouter(Info)
