import React from 'react'
import {
  Layout,
  Select,
  Button,
  Table,
  Empty,
  message,
  Icon,
  Modal,
  Input,
  Form
} from 'antd'
import { FormComponentProps } from 'antd/lib/form'
import { withRouter, RouteComponentProps } from 'react-router-dom'
import moment from 'moment'
import Http, { QueryResult, QueryRequest } from '../http'
import BaseVO from '../types'
import { toLine } from '../util'

const { Content } = Layout

type IProps = RouteComponentProps & {
  /**
   * 
   */
  httpPath: string
  /**
   * 
   */
  modelName: string
}

interface IState {
  loading: boolean
  selectedRowKeys: string[] | number[]
  /**
   * 
   */
  data: QueryResult<BaseVO>
  pageSize: number | undefined
  current: number | undefined
  /**
   * 
   */
  param: {
    conditions: any | undefined
    sorters: any | undefined
  }
  /**
   * 0- 1- 2-
   */
  modalFlag: number
  modalData: BaseVO | undefined
}

class List extends React.Component<IProps, IState> {
  form: React.ReactElement<FormProps> | undefined = undefined
  constructor(props: IProps) {
    super(props)
    this.state = {
      loading: true,
      selectedRowKeys: [],
      data: {
        total: undefined,
        content: []
      },
      pageSize: 10,
      current: 1,
      param: {
        conditions: {},
        sorters: {}
      },
      modalFlag: 0,
      modalData: undefined
    }
  }

  componentDidMount() {
    this.handleListChange()
  }

  handleListChange = () => {
    const { httpPath } = this.props
    this.setState({ loading: true }, () => {
      const { pageSize, current, param } = this.state
      const queryRequest: QueryRequest = {
        pageSize,
        current,
        ...param
      }
      Http.post(`${httpPath}/list`, queryRequest)
        .then(res => {
          this.setState({ loading: false, data: res.data.data })
        })
        .catch(error => {
          this.setState({ loading: false })
        })
    })
  }

  handleSelectChange = value => {
    const { conditions } = this.state.param
    conditions['keywords'] = value
    this.setState({ current: 1 })
    this.handleListChange()
  }

  handleDeleteOper = async (ids: Array<string> | Array<number>) => {
    const { httpPath } = this.props
    Modal.confirm({
      title: '?',
      okText: '',
      cancelText: '',
      onOk: () => {
        let queryString = ''
        ids.forEach(id => (queryString = queryString.concat(`ids=${id}&`)))
        Http.delete(
          `${httpPath}?${queryString.substring(0, queryString.length - 1)}`
        ).then(res => {
          if (res.data.status === '00000000') {
            message.success('')
            this.handleListChange()
          } else {
            message.error(res.data.message)
          }
        })
      }
    })
  }

  renderSearchBar() {
    const { modelName } = this.props
    return (
      <Select
        mode={'tags'}
        placeholder={`${modelName}`}
        style={{ width: '280px' }}
        tokenSeparators={[' ']}
        showArrow={true}
        suffixIcon={<Icon style={{ fontSize: '16px' }} type="search" />}
        onChange={this.handleSelectChange}
        notFoundContent={null}
      />
    )
  }

  renderButtonBar() {
    const { selectedRowKeys } = this.state
    return (
      <div>
        <Button
          className="ele-operation"
          type="primary"
          onClick={event => {
            event.preventDefault()
            this.setState({ modalFlag: 1 })
          }}
        >
          
        </Button>
        <Button
          type="ghost"
          disabled={selectedRowKeys.length < 1}
          onClick={() => {
            this.handleDeleteOper(selectedRowKeys)
          }}
        >
          
        </Button>
      </div>
    )
  }

  buildColumns() {
    const columns = [
      {
        title: '',
        key: 'index',
        render: (text, record, index) => {
          return index + 1
        },
        onHeaderCell: column => ({
          style: { textAlign: 'center', width: '5%' }
        }),
        onCell: (record, rowIndex) => ({ style: { textAlign: 'center' } })
      },
      {
        title: '',
        key: 'name',
        dataIndex: 'name',
        onHeaderCell: column => ({
          style: { textAlign: 'center' }
        }),
        onCell: (record, rowIndex) => ({ style: { textAlign: 'center' } })
      },
      {
        title: '',
        key: 'createTime',
        dataIndex: 'createTime',
        render: (text, record, index) => {
          return moment(record.createTime).format('YYYY-MM-DD HH:mm:ss')
        },
        sorter: (a, b) => {
          return a.id - b.id
        },
        onHeaderCell: column => ({
          style: { textAlign: 'center', width: '15%' }
        }),
        onCell: (record, rowIndex) => ({ style: { textAlign: 'center' } })
      },
      {
        title: '',
        key: 'operation',
        render: (text, record, index) => {
          return (
            <div>
              <Button
                type={'link'}
                onClick={event => {
                  event.preventDefault()
                  this.setState({ modalFlag: 2, modalData: record })
                }}
              >
                
              </Button>
              <Button
                type={'link'}
                onClick={event => {
                  event.preventDefault()
                  this.handleDeleteOper([record.id])
                }}
              >
                
              </Button>
            </div>
          )
        },
        onHeaderCell: column => ({
          style: { textAlign: 'center', width: '16%' }
        }),
        onCell: (record, rowIndex) => ({ style: { textAlign: 'center' } })
      }
    ]
    return columns
  }

  renderTable() {
    const { loading, selectedRowKeys, data, pageSize, current } = this.state
    const { total, content } = data
    return loading || data.content.length > 0 ? (
      <Table
        rowKey={record => {
          return record.id
        }}
        rowSelection={{
          columnWidth: '30px',
          selectedRowKeys,
          onChange: selectedRowKeys => {
            this.setState({ selectedRowKeys })
          }
        }}
        columns={this.buildColumns()}
        dataSource={content}
        loading={loading}
        pagination={{
          total,
          current,
          pageSize,
          pageSizeOptions: ['10', '20', '50'],
          showSizeChanger: true,
          showTotal: total => {
            return ` ${total} `
          }
        }}
        onChange={(pagination, filters, sorter, extra) => {
          const { param } = this.state
          const { current, pageSize } = pagination
          const { field, order } = sorter
          param.sorters = {}
          if (field && order) {
            param.sorters[toLine(field)] = fetchOrderDirection(order)
          }
          this.setState({ current, pageSize })
          this.handleListChange()
        }}
      />
    ) : (
      <Empty description={''} />
    )
  }

  handleOk = e => {
    e.preventDefault()
    const { httpPath } = this.props
    const { modalFlag } = this.state
    this.form &&
      this.form.props.form.validateFields((err, values) => {
        if (!err) {
          // Http.put(),Http.post()
          const method =
            modalFlag === 1 ? Http.put : modalFlag === 2 ? Http.post : undefined
          method &&
            method(`${httpPath}`, values)
              .then(res => {
                if (res.data.status === '00000000') {
                  message.success(``)
                  this.handleListChange()
                } else {
                  message.error(res.data.message)
                }
              })
              .catch(err => {
                debugger
                message.error(err.response.data.msg)
              })
          this.setState({
            modalFlag: 0
          })
        }
      })
  }

  handleCancel = e => {
    this.setState({
      modalFlag: 0,
      modalData: undefined
    })
  }

  renderAddModal() {
    const { modalFlag, modalData } = this.state
    const title = `${
      modalFlag === 1 ? '' : modalFlag === 2 ? '' : undefined
    }`
    return (
      <Modal
        title={title}
        visible={modalFlag > 0}
        okText={''}
        cancelText={''}
        onOk={this.handleOk}
        onCancel={this.handleCancel}
      >
        <WrappedNormalForm
          wrappedComponentRef={(form: React.ReactElement<FormProps>) => {
            this.form = form
            if (this.form) {
              if (modalFlag === 2 && modalData) {
                // 
                this.form.props.form.setFieldsValue({ ...modalData })
              } else if (modalFlag === 1) {
                // 
                this.form.props.form.resetFields(['id', 'name'])
              }
            }
          }}
        />
      </Modal>
    )
  }

  render() {
    return (
      <Content>
        <div className="list-page">
          <div className="list-page-content">
            <div className="list-page-content-toolbar">
              <div className="list-page-content-toolbar-search">
                {this.renderSearchBar()}
              </div>
              <div className="list-page-content-toolbar-button">
                {this.renderButtonBar()}
              </div>
            </div>
            <div className="list-page-content-table">{this.renderTable()}</div>
          </div>
        </div>
        <div>{this.renderAddModal()}</div>
      </Content>
    )
  }
}

function fetchOrderDirection(order: string) {
  if (order === 'descend') {
    return 'desc'
  } else {
    return 'asc'
  }
}

export default withRouter(List)

const formItemLayout = {
  labelCol: {
    xs: { span: 20 },
    sm: { span: 4 }
  },
  wrapperCol: {
    xs: { span: 20 },
    sm: { span: 16 }
  }
}

// const tailFormItemLayout = {
//   wrapperCol: {
//     xs: {
//       span: 2,
//       offset: 0
//     },
//     sm: {
//       span: 2,
//       offset: 4
//     }
//   }
// }

interface FormProps extends FormComponentProps {}

interface FormState {}

class NormalForm extends React.Component<FormProps, FormState> {
  render() {
    const { getFieldDecorator } = this.props.form
    return (
      <Form {...formItemLayout}>
        {getFieldDecorator('id')(<Input hidden />)}
        <Form.Item key={'name'} label="">
          {getFieldDecorator('name', {
            rules: [{ required: true, message: `` }]
          })(<Input />)}
        </Form.Item>
      </Form>
    )
  }
}

const WrappedNormalForm = Form.create({
  name: 'normal_login'
})(NormalForm)
